/** @format */

body {
  font-size: 32px;
  font-family: 'Pacifico', cursive;
}
.main {
  height: 100vh;
}
@media only screen and (max-width: 576px) {
  .container {
    border: none !important;
  }
}
.lead {
  font-family: Arial, Helvetica, sans-serif;
}
.footer {
  text-align: center;
  background-color: rgb(63, 64, 65);
  position: fixed;
  padding: 1.5rem;
  bottom: 0;
  width: 100%;
  left: 50%;
  transform: translateX(-50%);
  font-family: Arial, Helvetica, sans-serif;
  font-size: 1rem;
}

.footer a:hover {
  color: red !important;
}
